.footer {
    background-color: rgb(230, 253, 250);
    height: auto;
    color: black;
    display: flex;
    justify-content: space-around;
    font-family: "Didact Gothic", sans-serif;
    list-style-type: none;
    text-align: center;
}
.info {
    list-style-type: none;

}


.ScheduleLink {
   
    border: 2px solid gray;
    border-radius: 5px;
    background-color: orange;
    color: black;
    margin: 1rem;
    padding: .2rem 0;
}
.ScheduleLink, a {
    text-decoration: none;
    color: black;
}


.hours {
    list-style-type: none;
    text-align: center;
    padding: 0;
   
}

